export default {
	data() {
		return {
			menus: [{
					name: '花朵图鉴',
					icon: '../../static/images/tujian.png',
					key: 1,
				},
				{
					name: '个人成就',
					icon: '../../static/images/chengjiu.png',
					key: 2,
				},
				{
					name: '近31天',
					icon: '../../static/images/zhongzhi.png',
					key: 3,
				},
				{
					name: '帮助中心',
					icon: '../../static/images/8.png',
					key: 4,
				},
				{
					name: '意见反馈',
					icon: '../../static/images/9.png',
					key: 5,
				},
				{
					name: '关于我们',
					icon: '../../static/images/10.png',
					key: 6,
				}
			]
		};
	},
	onLoad() {
		
	},
	onShow() {
		if(this.$store.state.userMess.length==0){		/* 如果用户信息未获取,则提示并返回首页 */
			var that=this;
			uni.showModal({
				title:"登录提示",
				content:"请先登录，点击确定返回首页",
				showCancel:false,		/* 不显示取消按钮 */
				success(res) {
					console.log("提示成功");
					if(res.confirm){
						that.goBackIndex()
					}
				},
				fail() {
					console.log("提示失败")
				}
			})
		}
	},
	methods: {
		goBackIndex:function(){
			uni.switchTab({
				url:'../index/index',
				success() {
					console.log("返回成功")
				},
				fail() {
					console.log("返回失败")
				}
			})
		},
		jumpto:function(index){
			if(index==0){
				uni.navigateTo({
				    url: '../achievement/achievement'
				});
			}
			else if(index==1){
				uni.navigateTo({
				    url: '../honor/honor'
				});
			}
			else if(index==2){
				uni.navigateTo({
				    url: '../monthFlower/monthFlower'
				});
			}
			else if(index==3){
				uni.navigateTo({
				    url: '../help/help'
				});
			}
			else if(index==4){
				uni.navigateTo({
				    url: '../feedback/feedback'
				});
			}
			else if(index==5){
				uni.navigateTo({
				    url: '../about/about'
				});
			}
		}
	},
	computed: {
		theUsername(){
			return this.$store.state.userMess.nickName;
		},
		theHeadImgUrl(){
			return this.$store.state.userMess.avatarUrl;
		}
	}
}
